body {
  /* font: 400 14px Roboto, sans-serif; */
  background: #222831;
  -webkit-font-smoothing: antialised;
}

input,
button,
textarea {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

form input {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

.button {
  width: 100%;
  height: 60px;
  background: gray;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #41413d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link svg {
  margin-right: 8px;
}

.back-link:hover {
  opacity: 0.8;
}

#sobre {
  color: #f2a365;
}
#card {
  background-color: #222831;
}
.card-content {
  background-color: #e7ecf1;
}
.card-action {
  background-color: #30475e;
}
.card-title {
  text-shadow: 1px 1px #f2a365;
}
.carinho {
  font-size: 32px;
  color: #30475e;
}
.carinho :hover {
  color: #f2a365;
}
.fotos {
  object-fit: cover;
  width: 50vh;
  height: 50vh;
}
