.login-container {
    /* position: absolute; */
    margin-top: 50px;
    background-color: #f0f0f5;
  
    border-radius: 10px 10px 10px 10px;
  }
  form input {
    color: #3b5998;
  }
  #erro {
    color: #dd4b39;
  }
  #adotei {
    font-family: "Tahoma";
  
    color: #3b5998;
  }
  #bemvindo {
    font-family: "Tahoma";
  
    color: #3b5998;
  }
  #textologin {
    font-family: "Tahoma";
    margin-top: 50px;
    color: #3b5998;
  }
  #cadastro {
    align-self: center;
    font-size: large;
    color: #3b5998;
  }
  /* Style all font awesome icons */
  .fa {
    padding: 20px;
    font-size: 30px;
    width: 171px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 10px 10px 10px 10px;
  }
  
  /* Add a hover effect if you want */
  .fa:hover {
    opacity: 0.7;
  }
  
  /* Set a specific color for each brand */
  
  /* Facebook */
  .fa-facebook {
    background: #3b5998;
    color: white;
  }
  
  .fa-google {
    background: #dd4b39;
    color: white;
  }
  